// Ð˜Ð¼Ð¿Ð¾Ñ€Ñ‚ Ð¸ÑÑ…Ð¾Ð´Ð½Ð¾Ð³Ð¾ Ð¼Ð°ÑÑÐ¸Ð²Ð°

import images from './gallery-items.js';

// Ð¡ÑÑ‹Ð»ÐºÐ¸

const galleryContainer = document.querySelector('.js-gallery');
const modal = document.querySelector('.js-lightbox');
const modalImage = document.querySelector('.lightbox__image');
const modalBtn = document.querySelector('[data-action="close-lightbox"]');
const overlay = document.querySelector('.lightbox__overlay');

galleryContainer.addEventListener('click', openModal);

galleryContainer.insertAdjacentHTML('beforeend', createGalleryMarkup(images));

// Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ Ð¸ Ñ€ÐµÐ½Ð´ÐµÑ€ Ñ€Ð°Ð·Ð¼ÐµÑ‚ÐºÐ¸ Ð¿Ð¾ Ð¼Ð°ÑÑÐ¸Ð²Ñƒ Ð´Ð°Ð½Ð½Ñ‹Ñ… Ð¸ Ð¿Ñ€ÐµÐ´Ð¾ÑÑ‚Ð°Ð²Ð»ÐµÐ½Ð½Ð¾Ð¼Ñƒ ÑˆÐ°Ð±Ð»Ð¾Ð½Ñƒ.

function createGalleryMarkup(images) {
  return images
    .map(({ original, preview, description }) => {
      return `
      <li class="gallery__item">
        <a
          class="gallery__link"
          href="${original}"
        >
          <img
            class="gallery__image"
            src="${preview}"
            data-source="${original}"
            alt="${description}"
          />
        </a>
      </li>
        `;
    })
    .join('');
}

// Ð ÐµÐ°Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ Ð´ÐµÐ»ÐµÐ³Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ñ Ð½Ð° Ð³Ð°Ð»ÐµÑ€ÐµÐµ ul.js - gallery Ð¸ Ð¿Ð¾Ð»ÑƒÑ‡ÐµÐ½Ð¸Ðµ url Ð±Ð¾Ð»ÑŒÑˆÐ¾Ð³Ð¾ Ð¸Ð·Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð¸Ñ.
// ÐžÑ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð° Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑƒ Ð½Ð° ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ðµ Ð³Ð°Ð»ÐµÑ€ÐµÐ¸.
// ÐŸÐ¾Ð´Ð¼ÐµÐ½Ð° Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ñ Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð° src ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° img.lightbox__image

function openModal(event) {
  event.preventDefault();
  if (event.target === event.currentTarget) {
    return;
  }
  window.addEventListener('keydown', onEscKeyPress);

  modalImage.src = event.target.dataset.source;
  modalImage.alt = event.target.alt;
  modal.classList.add('is-open');
  modalBtn.addEventListener('click', closeModal);
  overlay.addEventListener('click', onOverlayClick);
  window.addEventListener('keydown', openNextImg);
  // window.addEventListener('keyup', moveRight);
  // window.addEventListener('keyup', moveLeft);
}

// Ð—Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð° Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑƒ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑƒ button[data - action= "close-lightbox"].
// ÐžÑ‡Ð¸ÑÑ‚ÐºÐ° Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ñ Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð° src ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° img.lightbox__image.

function closeModal() {
  modal.classList.remove('is-open');
  modalImage.src = '';

  window.removeEventListener('keydown', onEscKeyPress);
  modalBtn.removeEventListener('click', closeModal);
  overlay.removeEventListener('click', onOverlayClick);
  window.removeEventListener('keydown', openNextImg);
  // window.removeEventListener('keyup', moveRight);
  // window.removeEventListener('keyup', moveLeft);
}

// Ð—Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð° Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑƒ Ð½Ð° div.lightbox__overlay.

function onOverlayClick(event) {
  if (event.target === event.currentTarget) {
    closeModal();
  }
}

// Ð—Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð° Ð¿Ð¾ Ð½Ð°Ð¶Ð°Ñ‚Ð¸ÑŽ ÐºÐ»Ð°Ð²Ð¸ÑˆÐ¸ ESC.

function onEscKeyPress(event) {
  if (event.code === 'Escape') {
    closeModal();
  }
}

// ÐŸÑ€Ð¾Ð»Ð¸ÑÑ‚Ñ‹Ð²Ð°Ð½Ð¸Ðµ Ð¸Ð·Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð¸Ð¹ Ð³Ð°Ð»ÐµÑ€ÐµÐ¸ Ð² Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¾Ð¼ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð¼ Ð¾ÐºÐ½Ðµ ÐºÐ»Ð°Ð²Ð¸ÑˆÐ°Ð¼Ð¸ "Ð²Ð»ÐµÐ²Ð¾" Ð¸ "Ð²Ð¿Ñ€Ð°Ð²Ð¾".

function openNextImg(event) {
  let i = images.findIndex(item => item.original === modalImage.src);

  if (event.code === 'ArrowLeft') {
    if (i === 0) {
      return closeModal();
    }
    i -= 1;
  }

  if (event.code === 'ArrowRight') {
    if (i === images.length - 1) {
      return closeModal();
    }
    i += 1;
  }

  modalImage.src = images[i].original;
  modalImage.alt = images[i].description;
}
